import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/Index'
import Login from '@/views/Login'
import Register from '@/views/Register'

import PlayList from '@/components/PlayList'
import My from '@/components/My'
import MusicLib from '@/components/MusicLib'
import Lyrics from '@/components/Lyrics'
import IFrame from '@/components/IFrame'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
      meta:{
        title:"首页",
        showBar:true
      }
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta:{
        title:"登陆",
        showBar:true//为true不显示,不写showBar相当于false
      }
    },
    {
      path: '/register',
      name: 'Register',
      component: Register,
      meta:{
        title:"注册",
        showBar:true
      }
    },
    {
      path: '/playlist',
      name: 'PlayList',
      component: PlayList,
      meta:{
        title:"音乐列表",
      }
    },
    {
      path: '/my',
      name: 'My',
      component: My,
      meta:{
        title:"我的",
      }
    },
    {
      path:"/musiclib",
      name:"MusicLib",
      component:MusicLib,
      meta:{
        title:"乐库"
      }
    },
    {
      path:"/lyrics",
      name:"Lyrics",
      component:Lyrics,
      meta:{
        title:"歌词"
      }
    },
    {
      path:"/frame",
      name:"IFrame",
      component:IFrame,
      meta:{
        title:"解你忧",
        showBar:true
      }
    }
  ]
})



