<template>
  <iframe class="frame" :src="id[this.$route.query.id]" frameborder="0"></iframe>
</template>

<script>
export default {
    name:"IFrame",
    data(){
      return{
        id:[
          "http://demo.kodcloud.com/#desktop",
          "https://www.aimoon.me/",
          "https://dam.musetransfer.com/share/eab4a7a70fc147388e41a1c07ed355d9/folder/487768"
        ]
      }
    },
    created(){
    }
}
</script>

<style>
.frame{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}
</style>


