import Vue from "vue"
import Vuex from "vuex"
import moduleUser from '@/store/user';
import moduleMusic from '@/store/music';

Vue.use(Vuex)
const store=new Vuex.Store({
    modules:{
        "m_user":moduleUser,
        "m_music":moduleMusic
    }
})
export default store


