export default{
    namespaced:true,
    state:()=>({
        playlist:JSON.parse(window.localStorage.getItem("playlist")),
        myplaylist:JSON.parse(window.localStorage.getItem("myplaylist"))
    }),
    mutations:{
        updatePlayList(state,playlist){
            state.playlist=playlist
            this.commit("m_music/savePlayListToStorage")
        },
        savePlayListToStorage(state){
            window.localStorage.setItem("playlist",JSON.stringify(state.playlist))
        },
        updateMyPlayList(state,myplaylist){
            state.myplaylist=myplaylist
            this.commit("m_music/saveMyPlayListToStorage")
        },
        saveMyPlayListToStorage(state){
            window.localStorage.setItem("myplaylist",JSON.stringify(state.myplaylist))
        }
    },
    getters:{
    }
}


