<template>
    <div class="imgs">
        <img class="img" :class="[ishover==1?'':'hover1',active==1?'active-my':'']" :src="imgs.my" @click="clickImg($event,'my',1)">
        <img class="img" :class="[ishover==2?'':'hover2',active==2?'active-playlist':'']" :src="imgs.playlist" @click="clickImg($event,'playlist',2)">
        <img class="img" :class="[ishover==4?'':'hover4',active==4?'active-lyrics':'']" :src="imgs.lyrics" @click="clickImg($event,'lyrics',4)">
        <img class="img" :class="[ishover==3?'':'hover3',active==3?'active-musiclib':'']" :src="imgs.musiclib" @click="clickImg($event,'musiclib',3)">

    </div>
</template>

<script>
import my from '@@/common/image/my.png';
import playlist from '@@/common/image/playlist.png';
import musiclib from '@@/common/image/musiclib.png';
import lyrics from '@@/common/image/lyrics.png';
export default {
    name: "LeftBar",
    data() {
        return {
            imgs: {
                my: my,
                playlist: playlist,
                musiclib: musiclib,
                lyrics: lyrics

            },
            active:2,
            ishover:2
            
        }
    },
    methods: {
        clickImg(e, pageName,index) {
            this.active=index
            this.ishover=index
            this.$router.push(pageName).catch(err=>err)
        }
    }

}
</script>

<style>
.imgs{
    position: fixed;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    width: 150px;
    height: 100%;
    padding: 5px 25px;
}
.img{
    width: 30px;
    margin: 25px 0;
}
/* hover */
.hover1:hover {
    content: url(./../../../static/common/image/my-hover.png);
    
}

.hover2:hover {
    content: url(./../../../static/common/image/playlist-hover.png);
}

.hover3:hover {
    content: url(./../../../static/common/image/musiclib-hover.png);
}

.hover4:hover {
    content: url(./../../../static/common/image/lyrics-hover.png);
}

/* active */
.active-my{
    content: url(./../../../static/common/image/my-active.png);
}
.active-playlist{
    content: url(./../../../static/common/image/playlist-active.png);
}
.active-musiclib{
    content: url(./../../../static/common/image/musiclib-active.png);
}
.active-lyrics{
    content: url(./../../../static/common/image/lyrics-active.png);
}
</style>


