<template>
  <div class="index-body" :style="myBgImgUrl">
    <div class="bg-mask"></div>
    <div class="index-containt">
      <div class="index-avatar">
        <img src="https://www.aimoon.me/upload/user/3/18163.jpg" alt="" srcset="">
        <span>JieNiYou</span>
      </div>
      <!-- 音乐播放器 -->
      <div v-show="isShowMusicList" class="musicPlayer">
        <i class="el-icon-error" @click="isShowMusicList = !isShowMusicList"></i>
        <aplayer :music="musicList[0]" :list="musicList" :showlrc="true" :lrcType="3"></aplayer>

      </div>
      <div class="index-right">
        <div class="index-right-item">
          <!-- 一言 -->
          <div class="index-aWord" v-show="!isShowMusic">
            <span @click="isShowMusic = !isShowMusic"><i class="music-icon"></i>打开音乐播放器</span>
            <div @click="queryAWord">
              <span>{{ aWord.hitokoto }}</span>
              <span>-「 {{ aWord.from_who || aWord.from }} 」</span>
            </div>
          </div>
          <div class="index-music" v-show="isShowMusic">
            <div>
              <div>
                <span @click="isShowMusicList = !isShowMusicList">音乐列表</span>
                <span @click="isShowMusic = !isShowMusic">回到一言</span>
              </div>
              <div>
                <div>
                  <span @click="isMusicPlay = true; previous()"><i class="previous"></i></span>
                  <transition name="fade">
                    <span @click="play()" v-if="!isMusicPlay"><i class="play"></i></span>
                    <span @click="pause()" v-if="isMusicPlay"><i class="pause"></i></span>
                  </transition>
                  <span @click="isMusicPlay = true"><i class="next"></i></span>
                </div>
                <div>
                  <span><el-slider v-model="volume" @input="changeVolume(volume)"></el-slider></span>
                  <span>{{ volume }}</span>
                </div>

              </div>
            </div>
          </div>
        </div>
        <div class="index-time">
          <span>{{ formatTime(nowTime).Date }}</span>
          <span>{{ formatTime(nowTime).Week }}</span>
          <span>{{ formatTime(nowTime).Time }}</span>
          <span>{{ local.city }}</span>
        </div>
      </div>

      <div class="index-others">
        <p><i class="el-icon-link"></i>网站收藏列表</p>
        <div>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 0 } }">云桌面</router-link>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 1 } }">《我爱月亮》免费观影</router-link>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 2 } }">解你忧の图库</router-link>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 0 } }">待写入</router-link>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 0 } }">待写入</router-link>
          <router-link target="_blank" :to="{ path: '/frame', query: { id: 0 } }">待写入</router-link>
        </div>

      </div>
      <div class="index-links">
        <router-link to="/playlist">音乐</router-link>
        <a target="_blank" href="https://jieniyou.github.io">博客-github</a>
        <a target="_blank" href="https://cnblogs.com/jieniyou">博客-博客园</a>
      </div>
    </div>
  </div>
</template>

<script>
import { jsonp } from 'vue-jsonp';
import aplayer from 'vue-aplayer';
import { getAWord } from './../api/aWord';
import { queryPlayListByNetWork } from './../api/music';

export default {
  name: "Index",
  components: {
    aplayer
  },
  data() {
    return {
      aWord: {},
      isMusicPlay: false,
      isShowMusic: false,
      isShowMusicList: false,
      volume: 25,
      musicList: [
        {
          artist: "CORSAK胡梦周/马吟吟",
          lrc: "http://api.wuenci.com/meting/api/?server=tencent&type=lrc&id=002eDlpu4U223F",
          pic: "http://api.wuenci.com/meting/api/?server=tencent&type=pic&id=002yOiJ347ly2o",
          showLrc: true,
          src: "http://api.wuenci.com/meting/api/?server=tencent&type=url&id=002eDlpu4U223F",
          title: "溯 (Reverse)feat. 马吟吟"
        }
      ],

      nowTime: new Date(),
      //当地信息
      local: {
        //ip地址所在城市
        city: "",
        // 所在城市温度
        temperature: {
          high: "",
          low: ""
        },
        // 天气类型
        type: ""

      },
      myBgImgUrl: {
        backgroundImage: `url(static/common/image/index-bg/bg${this.RandomImgUrl()}.jpg)`
      }
    }
  },
  methods: {
    /**
     * 获得一言
     */
    queryAWord() {
      getAWord().then(res => {
        if (res.status == 200) {
          this.aWord = res.data
        }
      })
    },
    /**
     * 音乐获取
     */
    getPlayListByNetWork() {
      const server = "tencent"; //netease: 网易云音乐; tencent: QQ音乐; kugou: 酷狗音乐; xiami: 虾米; kuwo: 酷我
      const type = "playlist"; //song: 单曲; playlist: 歌单; album: 唱片
      const id = "1247511647"; //封面 ID / 单曲 ID / 歌单 ID
      queryPlayListByNetWork(server, type, id,
        successRes => {
          this.musicList = successRes.map(item => {
            return {
              src: item.url,
              title: item.name,
              showLrc: item.showlrc,
              artist: item.artist,
              pic: item.pic,
              lrc: item.lrc
            }
          })
          console.log(this.musicList)

        },
        errorRes => {
          this.$message({
            type: "warning",
            message: "歌曲信息加载失败"
          })
        })
    },
    /**
     * 音乐自定义控制
     */
    previous() {

    },
    play(){
      var audio = document.querySelector("audio")
      this.isMusicPlay = !this.isMusicPlay;
      audio.play()
    },
    pause(){
      var audio = document.querySelector("audio")
      this.isMusicPlay = !this.isMusicPlay;
      audio.pause()
    },
    spaceEvent(e){
      var audio = document.querySelector("audio")
      if (e.code==="Space") {
        if(audio.paused){
        this.play()
      }else{
        this.pause()
      }
      }      
    },
    /**
     * change	值改变时触发（使用鼠标拖曳时，只在松开鼠标后触发）
     * input	数据改变时触发（使用鼠标拖曳时，活动过程实时触发）
     * @param {Integer} volume 
     */
    changeVolume(volume) {
      var audio = document.querySelector("audio")
      audio.volume=volume/100
    },
    /**
     * 格式化时间
     */
    formatTime() {
      var date = new Date()

      var year = date.getFullYear()
      var month = (date.getMonth() + 1) < 10 ? '0' + (date.getMonth() + 1) : (date.getMonth() + 1)
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()

      var hour = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      var min = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      var sec = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()

      var week = date.getDay()
      var weekArr = ["星期天", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六"]
      var total = {
        "Date": year + ' 年 ' + month + ' 月 ' + day + ' 日',
        "Week": weekArr[week],
        "Time": hour + ":" + min + ":" + sec
      }
      return total;
    },
    /**
     * 根据ip获取位置信息
     */
    getLocalCity() {
      var data = {
        key: "6CQBZ-DWL3I-4W7GW-URHXN-PKLBZ-FXBFO",//申请的密钥,写死就行
      }
      var url = "https://apis.map.qq.com/ws/location/v1/ip"//这个就是地理位置信息的接口
      data.output = 'jsonp'
      jsonp(url, data).then(res => {
        this.local.city = res.result.ad_info.city
        // 根据城市，获取当前天气和时间（在此先预留获取天气方法，完成后取消注释）
        // this.getLocalWeather(this.local.city)
        return res
      }).catch(err => {
        this.$message({
          type: "error",
          message: "获取位置信息失败"
        })
      })
    },
    /**
     * 根据城市获取天气信息
     * @param {String} city 
     */

    /**
     * 随即背景图
     */
    RandomImgUrl() {
      return Math.floor(Math.random() * 10) < 10 ? '0' + Math.floor(Math.random() * 10) : Math.floor(Math.random() * 10)
    }
  },
  created() {
    this.RandomImgUrl();
    this.getLocalCity();
    this.queryAWord();
    this.getPlayListByNetWork();
    /* 键盘事件在组建创建时先绑定 */
    document.addEventListener('keyup',this.spaceEvent)
  },
  mounted() {
    let that = this
    /**
     * 挂载时间
     */
    this.timer = setInterval(function () {
      that.nowTime = new Date().toLocaleString()
    })
  },
  beforeDestroy() {
    /**
     * 销毁定时器
     */
    if (this.timer) {
      clearInterval(this.timer)
    }
    if (this.timeId) {
      clearTimeout(this.timeId)
      this.timeId = null
    }
    /* 销毁组件时，再移除该事件 */
    document.removeEventListener('keyup',this.spaceEvent)
  }
}
</script>

<style>
/* 首页主题内容 */
.index-containt {
  position: relative;
  z-index: 9999;
  width: 100%;
  height: 100vh;
}

/* 首页大头像 */
.index-avatar {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 150px;
  left: 150px;
  max-width: 350px;
  width: 350px;
  padding: 5px 10px ;
  transition: all .5s;
}

.index-avatar>img {
  border-radius: 50px;
  transition: all .5s;
}

.index-avatar>img:hover {
  transform: rotate(360deg);
  transition: all .5s;
}
.index-avatar>span{
  font-family: "Pacifico";
  font-size: 5rem;
  color: wheat;
}
@font-face {
  font-family: "Pacifico";
  src: url(./../../static/common/ttf/Pacifico.ttf);
}
/* 首页右上方的样式 */
.index-right {
  position: absolute;
  display: flex;
  top: 100px;
  right: 150px;
  width: 700px;
  justify-content: space-between;
}

.index-right>* {
  width: 340px;
  display: flex;
  color: white;
  background: #00000075;
  font-size: 1.1rem;
  border-radius: 5px;
  transition: all .3s;
}

.index-right>*:hover {
  transform: scale(1.02);
  transition: all .3s;
}

/* 首页一言和音乐 */
.index-right-item {}

/* 音乐图标自定义 */
.music-icon {
  background: url(./../../static/common/svg/music.svg) center center no-repeat;
  background-size: 20px;
  margin-right: 10px;
}

.music-icon::before {
  content: "11";
  visibility: hidden;
}

/* 一言 */
.index-aWord {
  width: 100%;
  height: 100%;
}

.index-aWord>span:first-child {
  display: block;
  background: #000000b6;
  opacity: 0;
  transition: all .3s;
  width: 100%;
  height: 35px;
  line-height: 35px;
  font-size: 15px;
}

.index-right-item:hover .index-aWord>span:first-child {
  opacity: .8;
  transition: all .3s;
}

.index-aWord>div {
  width: calc(100% - 30px);
  height: calc(100% - 65px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
}

.index-aWord>div>span:first-child {
  text-align: left;
}

.index-aWord>div>span:last-child {
  text-align: right;
}

/* 音乐box */
.index-music {
  width: 100%;
  padding: 15px 20px;
}

.index-music>div>div:first-child {
  display: flex;
  justify-content: space-around;
}

.index-music>div>div:first-child>span {
  background: #5a5a5ac0;
  padding: 2px 10px;
  border-radius: 10px;
}

/* 音乐 按钮 */
.index-music>div>div>div:nth-child(1) {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 15px;
}

:root {
  --music-play: url(./../../static/common/svg/play.svg);
  --music-pause: url(./../../static/common/svg/pause.svg);
  --music-previous: url(./../../static/common/svg/previous.svg);
  --music-next: url(./../../static/common/svg/next.svg);
}

.previous {
  display: block;
  content: "";
  width: 2.0rem;
  height: 2.0rem;
  background: var(--music-previous) center center no-repeat;
  background-size: cover;

}

.next {
  display: block;
  content: "";
  width: 2.0rem;
  height: 2.0rem;
  background: var(--music-next) center center no-repeat;
  background-size: cover;
}

.play {
  display: block;
  content: "";
  width: calc(2.25rem + 8px);
  height: calc(2.25rem + 8px);
  background: var(--music-play) center center no-repeat;
  background-size: cover;

}

.pause {
  display: block;
  content: "";
  width: calc(2.25rem + 8px);
  height: calc(2.25rem + 8px);
  background: var(--music-pause) center center no-repeat;
  background-size: cover;

}

/* 音量或歌名 */
.index-music>div>div>div:nth-child(2) {
  padding: 10px 35px;
}

/**
 * 自定义滑块
 * slider__bar 已过
 * slider__button 滑点
 * slider__runway 未过
*/
.el-slider__bar {
  background-color: #5a5a5ac0;
}

.el-slider__button {
  width: 10px;
  height: 10px;
  border: 2px solid #ffffff91;
  background-color: rgba(255, 255, 255, 0.788);
}

.el-slider__runway {
  background-color: #000000;
}

/* 音乐播放器 */
.musicPlayer {
  z-index: 999999;
  position: absolute;
  top: 15px;
  left: 15px;
  width: calc(100% - 30px);
  height: calc(100% - 30px);
  background: #00000075;
  border-radius: 15px;

}

.musicPlayer>i {
  z-index: 99999;
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 2.21rem;
  color: #ffffff2a;
  transition: all .3s;
}

.musicPlayer>i:hover {
  transform: rotate(-90deg);
  transition: all .3s;
}

/**自定义播放器 */
/* 歌词 */
.aplayer-lrc:before {
  background: -webkit-gradient(linear, left top, left bottom, from(#22d30b7e), to(hsla(0, 0%, 100%, 0))) !important;
}

.aplayer-lrc:after {
  background: -webkit-gradient(linear, left top, left bottom, from(hsla(0, 0%, 100%, 0)), to(hsla(113, 100%, 50%, 0.541))) !important;
}

.aplayer.aplayer-withlist .aplayer-body .aplayer-info {
  border-bottom: 1px solid rgba(148, 71, 71, 0.37) !important;
}

.aplayer {
  background: #000000f3 !important;
  color: #a8c0e4 !important;
}

.aplayer .aplayer-body {
  /* position: fixed !important; */
}

.aplayer-list {
  position: relative;
  height: 500px !important;

}

.aplayer-list ol {
  height: 500px !important;
  overflow-y: scroll !important;
}

.aplayer-list ol li {
  border-top: none !important;
}

.aplayer-list ol li.aplayer-list-light,
.aplayer-list ol li:hover {
  background: #98d00b38 !important;
}

/* 滚动条 */
.aplayer-list ol::-webkit-scrollbar-thumb {
  background-color: #98d00b !important;
}

.aplayer-list ol::-webkit-scrollbar-track {
  background-color: #00000075 !important;
}

/* 首页日期时间 */
.index-time {
  width: 200px;
  flex-direction: column;
  padding: 20px 70px;
}

@font-face {
  font-family: "unidreamLED";
  src: url(./../../static/common/ttf/unidreamled.ttf);
}

.index-time>span:nth-child(3) {
  font-family: "unidreamLED";
  font-size: 3.25rem;
  letter-spacing: 2.5px;
}

/* 首页其他项 */
.index-others {
  position: absolute;
  float: left;
  width: 700px;
  min-height: 45vh;
  bottom: 50px;
  right: 150px;
  text-align: left;
  font-size: 1.1rem;
  letter-spacing: 2px;
  color: rgba(255, 255, 255, 0.801);
}

.index-others>div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

}

.index-others>div>* {
  width: 200px;
  text-align: center;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffffb7;
  background: #0505056c;
  margin-bottom: 15px;
  border-radius: 15px;
  text-decoration: none;
  transition: all .5s;
}

.index-others>div>*:hover {
  transform: scale(1.05);
  transition: all .5s;
}

/* 首页相关链接 */
.index-links {
  position: absolute;
  bottom: 50px;
  left: 150px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  width: 250px;
  padding: 25px 0;
  background: #00000075;
  border-radius: 15px;

}

.index-links>* {
  display: block;
  width: 200px;
  height: 40px;
  line-height: 40px;
  font-family: '华文彩云';
  background: linear-gradient(-45deg, #a8c0e4a1 0%, #d6eef2ab 35%, #fad2e0a9 70%, #d8c2dfa6 100%);
  border-radius: 10px;
  color: #98d00b;
  text-decoration: none;
  margin-top: 12px;
  transition: all .3s;
}

.index-links>*:hover {
  width: 210px;
  color: #00d9ffe5;
  background: linear-gradient(45deg, #a8c0e4 0%, #d6eef2 35%, #fad2e0 70%, #d8c2df 100%);
  transition: all .3s;
}

/* 首页背景图相关 */
.index-body {
  display: flex;
  /* background-image: url(./../../static/common/image/index-bg/bg05.jpg); */
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
}

/* 首页遮罩 */
.bg-mask {
  position: fixed;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, .5);
}</style>


