<template>
  <div class="loginPage">
    <el-form :model="loginForm" status-icon :rules="rules" @keyup.enter.native="submitForm('loginForm')" ref="loginForm" label-width="100px" class="my-form">
      <h1>解你忧-音乐登录</h1>
      <el-form-item prop="userName">
        <span slot="label">
          <span class="label">用户名</span>
        </span>
        <el-input v-model="loginForm.userName" class="my-input"></el-input>
      </el-form-item>
      <el-form-item prop="userPassword">
        <span slot="label">
          <span class="label">密码</span>
        </span>
        <el-input type="password" v-model="loginForm.userPassword" autocomplete="off" class="my-input"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button class="btn-submit" type="primary" @click="submitForm('loginForm')">提交</el-button>
        <el-button class="btn-to" @click="toRegister()">去注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {login} from "../api/login"
import {mapMutations} from 'vuex';
export default {
  name: 'Login',
  data() {
    var checkUserName = (rule, value, callback) => {
        if (value==='') {
          return callback(new Error('用户名不能为空'));
        }
        setTimeout(() => {
            if (value.length < 6) {
              callback(new Error('须达到6个字符'));
            } else {
              callback();
            }
        }, 1000);
      };
    var checkUserPassword = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        callback();
      }
    };
    return {
      loginForm: {
        userName: '',
        userPassword: ''
      },
      rules: {
        userName: [
          { validator: checkUserName, trigger: 'blur' }
        ],
        userPassword: [
          { validator: checkUserPassword, trigger: 'blur' }
        ]
   
      }
    }
  },
  methods: {
    ...mapMutations("m_user",["updateUserInfo","updateToken"]),
    submitForm(formName) {
      const that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          login(this.loginForm).then(res=>{
            let data=res.data
            if (data.status==200) {
              that.updateUserInfo(data.data)
              that.updateToken(data.msg)
              /* 路由返回 */
              that.$router.back()
            }else{
              this.$message({
                type:"error",
                message:"账号或密码错误"
              })
            }
            
          }).catch(err=>{
            this.$message({
                type:"error",
                message:"网络繁忙"
              })
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    toRegister() {
      // this.$refs[formName].resetFields();
      this.$router.push("/register")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import url("./../../static/common/css/login.css");
</style>



