import request from '../request';
import $ from './../../static/common/js/jquery';
export function queryPlayList() {
    return request.get("/music/queryPlayList")
}
export function queryPlayListByUserId(userId) {
    return request.get("/music/queryPlayListByUserId/"+userId)
}

export function queryPlayListByNetWork(server,type,id,successFun,errorFun) {
    return $.ajax({
        url:"https://api.wuenci.com/meting/api/?server=" + server + "&type=" + type + "&id=" + id,
        type:"get",
        dataType:"JSON",
        success:function(res){successFun(res)},
        error:function(res){errorFun(res)}
    })
}


