<template>
  <div class="page-body">

    <div>
      <div class="div-title">
        <span>歌单</span>
      </div>
      <div class="playlists">
      <div v-for="(item, index) in myplaylist" :key="index">
        <img height="250px" :src="item.coverImgUrl">
      </div>
    </div>
    </div>
    
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import { queryPlayListByUserId } from "@/api/music"
export default {
  name: "My",
  data() {
    return {
      // myplaylist:window.localStorage.getItem("myplaylist")
    }
  },
  methods: {
    ...mapMutations("m_music",["updateMyPlayList"]),
    getPlayListByUserId() {
      let userId = this.userInfo.id
      let that = this
      queryPlayListByUserId(userId).then(res => {
        let data = res.data.data
        // console.log("data==>",data)
        if (res.status == 200) {
          that.updateMyPlayList(data.playlist)
          // console.log(that.myplaylist)
        } else {
          this.$message({
            message: res.data.msg,
            type: "error"
          })
        }
      }).catch(res => {
        console.log(res)
        this.$message({
          message: "服务器繁忙",
          type: "error"
        })
      })
    }
  },
  computed: {
    ...mapState("m_user", ['userInfo']),
    ...mapState("m_music", ['myplaylist']),

    
  },
  mounted() {
    this.getPlayListByUserId();
  }
}
</script>

<style>
@import url(./../../static/common/css/pages.css);
.div-title{
  display: flex;
  padding-left: 5px;
  
}
.div-title>span{
  width: 100px;
  height: 50px;
  line-height: 50px;
  border-radius: 15px;
  background: #51515157;
  margin-right: 10px;
  margin-bottom: 15px;
  cursor: default;
  transition: all 1s;
}
.div-title>span:hover{
  background: #696969;
    transition: all 1s;
}
.playlists {
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
}
.playlists>div>img {
  border-radius: 15px;
  margin: 5px 10px;
}
</style>


