<template>
    <div class="regPage">
        <el-form :model="regForm" status-icon :rules="rules" @keyup.enter.native="submitForm('regForm')" ref="regForm"
            label-width="100px" class="my-form">
            <h1>解你忧-音乐注册</h1>
            <el-form-item prop="userName">
                <span slot="label">
                    <span class="label">用户名</span>
                </span>
                <el-input v-model="regForm.userName" class="my-input"></el-input>
            </el-form-item>
            <el-form-item prop="userPassword">
                <span slot="label">
                    <span class="label">密码</span>
                </span>
                <el-input type="password" v-model="regForm.userPassword" autocomplete="off" class="my-input"></el-input>
            </el-form-item>
            <!-- <el-form-item prop="userNickName">
                <span slot="label">
                    <span class="label">昵称</span>
                </span>
                <el-input v-model="regForm.userNickName" class="my-input"></el-input>
            </el-form-item> -->
            <el-form-item prop="userEmail">
                <span slot="label">
                    <span class="label">邮箱</span>
                </span>
                <el-input type="email" v-model="regForm.userEmail" class="my-input"></el-input>
            </el-form-item>
            <!-- <el-form-item prop="userPhone">
                <span slot="label">
                    <span class="label">手机</span>
                </span>
                <el-input type="tel" v-model="regForm.userPhone" class="my-input"></el-input>
            </el-form-item> -->

            <el-form-item>
                <el-button class="btn-submit" type="primary" @click="submitForm('regForm')">提交</el-button>
                <el-button class="btn-to" @click="toLogin()">去登陆</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
  
<script>
import { register } from '@/api/login';
export default {
    name: 'Login',
    data() {
        var checkUserName = (rule, value, callback) => {
            if (value === '') {
                return callback(new Error('用户名不能为空'));
            }
            setTimeout(() => {
                if (value.length < 6) {
                    callback(new Error('须达到6个字符'));
                } else {
                    callback();
                }
            }, 1000);
        };
        var checkUserPassword = (rule, value, callback) => {
            const passwordRule = /^(?!([a-zA-Z]+|\d+)$)[a-zA-Z\d]{6,20}$/;
            if (value === '') {
                callback(new Error('请输入密码'));
            }
            setTimeout(() => {
                if (!passwordRule.test(value)) {
                    callback(new Error("密码6-20位,需包含数字和字母"));
                } else {
                    callback();
                }
            }, 500)
        };
        var checkUserEmail = (rule, value, callback) => {
            const emailRule = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
            if (value === "") {
                callback(new Error("请填写此字段"))
            }
            setTimeout(() => {
                if (!emailRule.test(value)) {
                    callback(new Error("邮箱格式错误!"))
                } else {
                    callback();
                }
            }, 1000);

        };
        return {
            regForm: {
                userName: '',
                userPassword: '',
                // userNickName: "",
                userEmail: "",
                // userPhone: ""
            },
            rules: {
                userName: [
                    { validator: checkUserName, trigger: 'blur' }
                ],
                userPassword: [
                    { validator: checkUserPassword, trigger: 'blur' }
                ],
                userEmail: [
                    { validator: checkUserEmail, trigger: 'blur' }
                ]
            },
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    register(this.regForm).then(res => {
                        let data = res.data
                        if (data.status == 200) {
                            this.$message({
                                type: "error",
                                message: "注册成功将在 3 秒后转入登陆界面"
                            })
                            setTimeout(() => {
                                /* 路由返回 */
                                this.$router.back()
                            }, 3000);

                        } else {
                            this.$message({
                                type: "error",
                                message: "账号已存在"
                            })
                        }

                    }).catch(err => {
                        this.$message({
                            type: "error",
                            message: "网络繁忙"
                        })
                    })
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        toLogin() {
            this.$router.push("/login")
        }
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url("./../../static/common/css/login.css");
</style>
  


