<template>
    <div class="box-body">
        <div class="box-left">
            <div class="box-item-left">
                <i class="el-icon-arrow-left" onclick="window.history.back()"></i>
                <i class="el-icon-arrow-right" onclick="window.history.forward()"></i>
            </div>
            <div class="box-item-right">
                <div class="box-item-span"></div>
                <div>
                    <el-input v-model="query" prefix-icon="el-icon-search" class="my-search" />
                </div>
            </div>
        </div>
        <div class="box-right">
            <i class="el-icon-setting"></i>
            <i class="el-icon-user-solid" v-if="!userInfo" @click="toLogin()"></i>
            <i v-else>
                <img width="50px" class="userAvatar" style="border-radius: 25px;" 
                :src="userInfo.userAvatar" @click="isShowUserInfoSelectControl">
                <transition name="fade">
                    <div class="userInfoSelect" v-if="isShowUserInfoSelect">
                    <!-- <span v-if="!netEaseId">绑定网易云账号</span>
                    <span v-else>网易id:{{ netEaseId }}</span> -->
                    <span @click="toLogout">退出</span>
                </div>
                </transition>
                
            </i>

        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex"
export default {
    name: "ToBar",
    data() {
        return {
            query: "",
            isShowUserInfoSelect:false
        }
    },
    methods: {
        ...mapMutations("m_user", ["updateUserInfo","updateToken"]),
        toLogin() {
            this.$router.push("/login")
        },
        toLogout() {
            const that = this
            that.updateUserInfo("")
            that.updateToken("")
        },
        isShowUserInfoSelectControl(){
            if(this.isShowUserInfoSelect){
                this.isShowUserInfoSelect=false
            }else{
                this.isShowUserInfoSelect=true
            }
           
        }
    },
    computed: {
        ...mapState("m_user", ["userInfo","token"])
    }
}
</script>

<style>
.box-body {
    z-index: 999999;
    position: fixed;
    display: flex;
    width: 100%;
    height: 80px;
    justify-content: space-between;
    line-height: 80px;
}

/* left */
.box-left {
    display: flex;
    margin-left: 150px;
    width: 400px;
    justify-content: center;
}

.box-item-left {
    display: flex;
    width: 120px;
    justify-content: space-between;
    align-items: center;
}

.box-item-left>i {
    border-radius: 50px;
    background: #51515157;
    width: 50px;
    height: 50px;
    line-height: 50px;
    font-size: 25px;
    color: #c4c4c454;
    font-weight: bold;
    transition: all 1s;
}

.box-item-left>i:hover {
    background: #696969;
    transition: all 1s;
}

.box-item-right {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.box-item-span {
    width: 1px;
    height: 15px;
    border-left: 1px solid #515151;
    margin: 0 20px;
}

.my-search .el-input__inner {
    border-radius: 25px !important;
    background: #51515157 !important;
    border-color: #51515157 !important;
    width: 240px !important;
    height: 50px !important;
    text-indent: 12px !important;
}

.my-search .el-input__inner:focus,
.my-search .el-input__inner:hover {
    border-color: #51515157 !important;
}

.el-input__icon {
    margin-left: 5px !important;
    font-size: 20px !important;
    font-weight: bold !important;
    color: #696969ab !important;
}

/* right */
.box-right {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-right: 25px;
    width: 130px;
}

.box-right>i {
    border-radius: 50px;
    background: #51515157;
    width: 50px;
    height: 50px;
    line-height: 50px;
    font-size: 25px;
    color: #c4c4c454;
    transition: all 1s;
}

.box-right>i:not(:last-child):hover {
    background: #696969;
    transition: all 1s;
}

.userAvatar{
    transition: all 1s;
}

.userAvatar:hover{
    transform: rotate(360deg);
    transition: all 1s;
}

/* 控制点击事件 v-if 的过渡时间 */
.fade-enter-active,.fade-leave-active{
    transition: opacity 1s;
}
.fade-enter,.fade-leave{
    opacity: 0;
}
/* userInfoSelect的样式 */
.userInfoSelect {
    position: relative;
    background: #ffffff2d;
    display: flex;
    flex-direction: column;
    text-align: left;
    font-size: 12px;
    width: calc(100% + 50px);
    right: 50px;
    top: 5px;
    border-radius: 10px;
    padding-left: 5px;
}
.userInfoSelect > span{
    cursor: pointer;
}
</style>


