<template>
    <div class="page-body">
        <div class="lists">
            <div v-for="(item, index) in playlist" :key="index">
                <img height="100px" :src="item.playlistCoverImg">
            </div>
        </div>

    </div>
</template>

<script>
import { queryPlayList } from "../api/music"
import {mapState,mapMutations} from 'vuex';
export default {
    name: "PlayList",
    data() {
        return {
        }
    },
    methods: {
        ...mapMutations("m_music",["updatePlayList"]),
        getPlayList() {
            let that = this
            queryPlayList().then(res => {
                // console.log(res)
                if (res.data.status == 200) {
                    // this.playlist = res.data.data
                    that.updatePlayList(res.data.data)
                } else {
                    this.$message({
                        message:res.data.msg,
                        type:"error"
                    })
                }
            }).catch(res=>{
                this.$message({
                        message:"服务器繁忙",
                        type:"error"
                    })
            })
        }
    },
    computed:{
        ...mapState("m_music",["playlist"])
    },
    mounted() {
        this.getPlayList()
    }
}
</script>

<style>
@import url(./../../static/common/css/pages.css);

.lists {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
}
.lists>div>img{
    border-radius: 15px;
    margin: 5px 5px;
}
</style>


