<template>
  <div id="app">
    <TopBar v-if="!$route.meta.showBar" />
    <LeftBar v-if="!$route.meta.showBar"/>
    <router-view/>
  </div>
</template>

<script>
import LeftBar from '@/components/common/LeftBar';
import TopBar from '@/components/common/TopBar';
import {Message} from "element-ui"
export default {
  name: 'App',
  components:{
    LeftBar,
    TopBar
  },
  created(){
    this.$nextTick(()=>{
      // 禁用右键
      // document.oncontextmenu = new Function('event.returnValue=false')
      // 禁用键盘
      // document.onkeydown = new Function('event.returnValue=false')
    })
    
    /**
     * 自定义右键和F12
     * @param {Object} e => 默认传来的事件参数
     */
    // 禁用右键
    document.oncontextmenu=function(e){
      Message({
        message:"为了浏览体验,本站禁止右键",
        center:true,
        iconClass:"el-icon-magic-stick",
        customClass:"myMessage"
      })
        e.returnValue=false
    }
     // 禁用F12键
    document.onkeydown=function(e){
      if(e&&e.keyCode===123){
        Message({
        message:"为了浏览体验,本站禁止F12键",
        center:true,
        iconClass:"el-icon-magic-stick",
        customClass:"myMessage"
      })
        e.returnValue=false
        return false
      }
    }
  }
}
</script>

<style>
#app {
  font-family: 'MiSans-Regular', sans-serif;
  text-align: center;
  color: #2c3e50;
}
@font-face {
  font-family: 'MiSans-Regular';
  src: url(./../static/common/ttf/MiSans-Regular.ttf);
}
html,body{
  background: black;
}
.myMessage{
  min-width: 200px;
  border-radius: 25px;
  color: #303030;
}
/* 自定义ElMessage文本 */
.myMessage .el-message__content{
  color: #7e7e7e;
  font-family: '华文行楷';
}
</style>



